.crypto-agency-home-section-1 {
    .progress {
        --bs-progress-height: 4px;
        --bs-progress-bg: #151c4e;
    }
    .progress-bar {
        background-color: var(--tc-theme-primary);
    }
}
.crypto-agency-home-section-2 {
    --bs-border-color: #222859;
}
.crypto-agency-home-section-3 {
    svg {
        margin-top: -10px;

        text {
            font-weight: 600;
            text-transform: uppercase;
            letter-spacing: -0.8px;
            fill: transparent;
            stroke: var(--tc-theme-primary);
            stroke-width: 2px;
            animation: animated-stroke 2s;
            text-anchor: middle;
            dominant-baseline: middle;
            @media (max-width: 575.98px) {
                text-anchor: start;
            }
        }
    }
}
.crypto-agency-home-section-4 {
    .card-service {
        &:hover {
            background:
                linear-gradient(#03061c, #03061c) padding-box,
                linear-gradient(to top, rgba(177, 227, 70, 0.3), rgba(26, 28, 76, 0)) border-box;
        }
    }
}
.crypto-agency-home-section-5 {
    --bs-border-color: #1b214a !important;
    .logo-event {
        min-height: 200px;
        a {
            opacity: 0.4;
            transition: all 0.3s ease;
        }
        &:hover {
            a {
                opacity: 1;
            }
        }
    }
}
.crypto-agency-home-section-6 {
    .card-service {
        &:hover {
            background:
                linear-gradient(#03061c, #03061c) padding-box,
                linear-gradient(to top, rgba(177, 227, 70, 0.3), rgba(26, 28, 76, 0)) border-box;
        }
    }
    .swiper-pagination-bullet {
        width: 6px;
        height: 6px;
        background: #5a5a5a;
    }
    .swiper-pagination-bullet-active {
        width: 6px;
        height: 6px;
        background: var(--tc-theme-primary);
    }
}
.crypto-agency-home-section-8 {
    // Swiper customize
    .swiper-button-next,
    .swiper-button-prev {
        --swiper-navigation-size: 16px;
        --swiper-navigation-top-offset: 50%;
        --swiper-navigation-sides-offset: 10px;
        --swiper-navigation-color: var(--tc-system-black);
        display: flex;
        width: 50px;
        height: 50px;
        padding: 0px;
        font-size: 16px;
        border-radius: 50px;
        background-color: #03061c;
        border: 1px solid #1f2446;
        svg {
            width: 16px;
            height: 16px;
        }
        &:hover,
        :active {
            background-color: var(--tc-theme-primary);
            border-color: var(--tc-theme-primary);
            svg {
                path {
                    fill: var(--tc-system-navy-blue-3);
                }
            }
        }
    }
}
.section-footer-26 {
    .form-control {
        &::placeholder {
            color: var(--tc-system-white);
            opacity: 0.3;
        }
    }
    .icon {
        svg {
            height: 14px;
            width: 14px;
            fill: var(--tc-system-white);
            opacity: 0.5;
        }
        &:hover {
            svg {
                fill: var(--tc-system-white);
                opacity: 1;
                transition-duration: 0.3s;
            }
        }
    }
}
.crypto-agency-home-section-4 {
    .swiper-slide {
        width: auto !important;
    }
}
.crypto-agency-home-section-7 {
    .download-app {
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
    }
}
.crypto-agency-about-section-1 {
    .solutions-list {
        .line {
            border-left: 1px dashed rgba(255, 255, 255, 0.3);
            margin-left: 30px;
        }
        .solutions-item {
            cursor: pointer;
            .icon-shape {
                background-color: rgba(255, 255, 255, 0.1);
                transition: all 0.3s ease-in-out;
            }
            &:hover {
                .icon-shape {
                    background-color: var(--tc-theme-primary);
                    svg {
                        path {
                            stroke: var(--tc-system-black);
                            transition: all 0.3s ease-in-out;
                        }
                    }
                }
            }
        }
    }
    .solutions-description-img {
        position: relative;
        .circle {
            width: 500px;
            height: 500px;
            border-radius: 500px;
            opacity: 0.5;
            background: linear-gradient(180deg, #50eefa 0%, rgba(255, 255, 255, 0) 100%);
        }
        .images {
            border-radius: 4px;
            background: rgba(255, 255, 255, 0.2);
            backdrop-filter: blur(10px);
        }
    }
}
.crypto-agency-about-section-4 {
    .btn-industries {
        transition: all 0.3s linear;
        background: rgba(255, 255, 255, 0.1);
        &:hover {
            background: var(--tc-theme-primary);
            .icon-shape {
                background: #03060a !important;
                svg {
                    path {
                        stroke: var(--tc-theme-primary);
                    }
                }
            }
            h6 {
                color: var(--tc-system-black);
            }
        }
    }
}
.crypto-agency-about-section-5,
.crypto-agency-blog-details-section-2 {
    .form-group {
        border-radius: 16px;
        border: 1px solid rgba(255, 255, 255, 0.1);
        background: rgba(255, 255, 255, 0.05);
        .form-control {
            color: var(--tc-system-white);
            border-radius: 16px;
            border: 1px solid rgba(255, 255, 255, 0.1);
            background: rgba(255, 255, 255, 0.05);
        }
        textarea {
            min-height: 170px;
        }
        .form-select {
            color: var(--tc-system-white);
            background-color: rgba(255, 255, 255, 0.05);
            border: 1px solid rgba(255, 255, 255, 0.1);
            border-radius: 16px;
            &:focus {
                outline: none;
                box-shadow: none;
            }
        }
    }
}
.crypto-agency-about-section-1,
.crypto-agency-our-services-section-1 {
    .row {
        --bs-gutter-x: 40px;
        --bs-gutter-y: 80px;
    }
}
.card-services {
    border-radius: 16px;
    background: rgba(255, 255, 255, 0.06);
    padding: 70px 32px 32px 32px;
    transition: all 0.2s linear;
    &:hover {
        background: var(--tc-theme-primary);
        a {
            h6 {
                transition: all 0.3s ease-in-out;
                color: var(--tc-system-black);
            }
        }
        p {
            color: var(--tc-system-black) !important;
        }
    }
    .icon-shape {
        border: 4px solid #03061c;
    }
    .card-body {
        padding-top: 24px;
    }
}
.card-industries {
    border-radius: 16px;
    border: 1px solid rgba(255, 255, 255, 0.1);
    background: rgba(255, 255, 255, 0.06);
    padding: 24px 24px 40px 24px;
    transition: all 0.2s linear;
    &:hover {
        border-color: var(--tc-theme-primary);
        a {
            h6 {
                transition: all 0.2s ease-in-out;
                color: var(--tc-theme-primary);
            }
        }
    }
    .card-img {
        border-radius: 16px;
    }
    .card-body {
        padding-top: 24px;
        .card-information {
            .time {
                position: relative;
                &::after {
                    position: absolute;
                    content: '';
                    width: 1px;
                    height: 14px;
                    transform: rotate(90deg);
                    background-color: rgba(255, 255, 255, 1);
                    opacity: 0.1;
                    top: 50%;
                    transform: translateY(-50%);
                    right: 0px;
                }
            }
        }
        .btn-linear {
            --bs-btn-bg: linear-gradient(#121529, #121529) padding-box, linear-gradient(to bottom, rgba(255, 255, 255, 1), rgba(26, 28, 76, 0)) border-box;
            --bs-btn-hover-bg: linear-gradient(#121529, #121529) padding-box, linear-gradient(to bottom, rgba(80, 238, 250, 1), rgba(26, 28, 76, 0)) border-box;
        }
    }
}
.card-contact {
    padding: 32px;
    transition: all 0.2s linear;
    border-radius: 16px;
    border: 1px solid rgba(255, 255, 255, 0.1);
    background: rgba(255, 255, 255, 0.05);
    .text-opacity-90 {
        opacity: 0.9;
    }
    &:hover {
        border-color: var(--tc-theme-primary);
    }
}
.card-pricing {
    border-radius: 16px;
    background: rgba(255, 255, 255, 0.06);
    padding: 32px;
}
.card-pricing-primary {
    border-radius: 16px;
    background: var(--tc-theme-primary);
    padding: 32px;
}
.card-team {
    transition: all 0.2s linear;
    .card-img {
        border-radius: 16px;
        border: 1px solid rgba(80, 238, 250, 0.2);
        background: rgba(255, 255, 255, 0.04);
        .logo {
            transition: all 0.2s linear;
            opacity: 0.34;
        }
        .social-group {
            transition: all 0.2s linear;
            transform: translateY(100%);
            opacity: 0;
            @media (max-width: 991.98px) {
                opacity: 1;
                transform: translateY(0);
            }
            .icon {
                display: inline-flex;
                justify-content: center;
                align-items: center;
                width: 44px;
                height: 44px;
                background: rgba(255, 255, 255, 0.2);
                border-radius: 50%;
                transition: all 0.2s linear;
                svg {
                    height: 22px;
                    width: 22px;
                    path {
                        fill: var(--tc-system-white);
                        opacity: 0.5;
                    }
                }
                &:hover {
                    background: var(--tc-theme-primary);
                    svg {
                        path {
                            fill: var(--tc-system-black);
                            opacity: 1;
                            transition-duration: 0.3s;
                        }
                    }
                }
            }
        }
    }
    .card-body {
        padding-top: 32px;
        .btn-share {
            transition: all 0.2s linear;
            background-color: var(--tc-system-white);
            &:hover {
                background-color: var(--tc-theme-primary);
            }
        }
    }
    &:hover {
        .logo {
            opacity: 1;
        }
        .social-group {
            opacity: 1;
            transform: translateY(0);
        }
    }
}
// accordion
.accordion {
    .card {
        border-radius: 16px;
        border: 1px solid rgba(255, 255, 255, 0.1);
        background: rgba(255, 255, 255, 0.06);
        transition: all 0.3s linear;
        overflow: hidden;
        .card-header {
            background: transparent;
            border-bottom: 0;
            a {
                border-top-left-radius: 16px;
                border-top-right-radius: 16px;
                transition: all 0.3s ease-in-out;
                .arrow {
                    &::before {
                        transform: rotate(0deg) !important;
                        font-family: 'remixicon';
                        content: '\ea4e';
                        transition: 0.3s;
                        color: var(--tc-system-white);
                        font-size: 24px;
                        font-weight: 300;
                    }
                }
                &.collapsed {
                    .arrow {
                        &::before {
                            content: '\ea4e';
                            transition: 0.3s;
                            transform: rotate(180deg) !important;
                        }
                    }
                }
            }
        }
    }
    & .active {
        border-color: var(--tc-theme-primary) !important;
    }
}
.pagination_item {
    background: rgba(255, 255, 255, 0.06);
    color: var(--tc-system-white);
    font-weight: 600 !important;
    border-radius: 10px !important;
    svg {
        path {
            stroke: var(--tc-system-white) !important;
            transition: 0.3s ease-in-out;
        }
    }
    &.active {
        background: var(--tc-theme-primary);
        color: var(--tc-system-black) !important;
        transition: 0.3s ease-in-out;
        svg {
            path {
                stroke: var(--tc-system-black) !important;
                transition: 0.3s ease-in-out;
            }
        }
    }
    &:hover {
        background: var(--tc-theme-primary);
        color: var(--tc-system-black) !important;
        transition: 0.3s ease-in-out;
        svg {
            path {
                stroke: var(--tc-system-black) !important;
                transition: 0.3s ease-in-out;
            }
        }
    }
    &:focus {
        box-shadow: none;
        background-color: var(--tc-theme-primary) !important;
        color: var(--tc-system-black) !important;
        transition: 0.3s ease-in-out;
        svg {
            path {
                stroke: var(--tc-system-black) !important;
                transition: 0.3s ease-in-out;
            }
        }
    }
}
